@import "../../style.scss";

.navBar {
  @include themify($themes) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 20px;
    height: 50px;
    border-bottom: 1px solid themed("border");
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: themed("bg");
    color: themed("textColor");

    .left {
      display: flex;
      align-items: center;
      gap: 30px;

      .icon {
        color: themed("textColor");
      }
      span {
        font-weight: bold;
        font-size: 20px;
        color: themed("logo");
      }
      .search {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 3px 3px;
        border: 1px solid themed("border");
        border-radius: 7px;

        input {
          border: none;
          width: 400px;
          outline: none;
          background-color: transparent;
          color: themed("textColor");

          @include tablet {
            width: 200px;
          }

          @include mobile {
            display: none;
          }
        }
      }
    }
    .right {
      display: flex;
      align-items: center;
      gap: 20px;
      @include mobile {
        display: none;
      }
      .user {
        display: flex;
        align-items: center;
        font-weight: 500;
        gap: 10px;
        @include tablet {
          display: none;
        }
        @include mobile {
          display: none;
        }
        img {
          width: 30px;
          height: 30px;
          object-fit: cover;
          border-radius: 50%;
        }
        span {
          font-size: 12px;
        }
      }
    }
  }
}
